.section1_container {
	box-sizing: border-box;
	background-color: #10223c;

	.section1_box {
		display: grid;
		padding: 13rem 0;
		grid-template-columns: 1fr 1fr;
		width: 100%;
		max-width: 100%;
		overflow: hidden;
		position: relative;

		@media only screen and (max-width:1600px){
			padding: 10rem 0;
		}
		@media only screen and (max-width:1400px){
			padding: 8rem 0;
		}
		@media only screen and (max-width:800px){
			grid-template-columns: 1fr .3fr;
			padding: 7rem 0;
		}


		@media only screen and (max-width:600px){
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding: 4rem 0;
		}
		@media only screen and (max-width:450px){
			padding: 2rem 0;
		}

		.titleimgmbl{
			display: block;
			@media only screen and (min-width: 601px){
				display: none;
			}
		}

		.data {
			display: flex;
			flex-direction: column;
			margin-left: 15%;
			width: 100%;
			flex-wrap: wrap;

			flex-grow: 0;
			max-width: 66.666667%;
			flex-basis: 66.666667%;

			@media only screen and (max-width:1600px){
				max-width: 77.666667%;
				flex-basis: 77.666667%;
			}
			@media only screen and (max-width:1400px){
				max-width: 88.666667%;
				flex-basis: 88.666667%;
			}
			@media only screen and (max-width:601px){
				max-width: 90%;
				flex-basis: 90%;
				margin:0 auto;
			}
			

			p {
				color: #f6d869;
				font-size: 4rem;
				font-weight: 500;
				font-family: system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', 'Liberation Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
				margin: 0;
				margin-bottom: 1rem;

				@media only screen and (max-width:800px){
					font-size: 3rem;
				}
				
			}
			.title{
				@media only screen and (max-width:600px){
					display: none;
				}
			}
			.discription1 {
				color: #fff;
				font-size: 2rem;

				@media only screen and (max-width:800px){
					font-size: 1.5rem;
				}
				@media only screen and (max-width:450px){
					font-size: 1.2rem;
				}
			}
			.discription2 {
				font-size: 3rem;

				@media only screen and (max-width:800px){
					font-size: 2rem;
				}
				@media only screen and (max-width:450px){
					font-size: 1.5rem;
				}
			}

			.btns {
				margin: 1.5rem 0;
				button {
					cursor: pointer;
					color: #f6d869;
					background-color: #0c1b30;
					font-size: 12px;
					text-align: center;
					font-family: 'Poppins';
					border: 2px solid #64592f;
					box-sizing: border-box;
					border-radius: 13px;
					box-shadow: 0 0 10px -3px #f6d869;
					padding: 0.5rem;
					margin: 0 0.3rem .5rem 0.3rem;
					width: 120px;

					@media only screen and (max-width:800px){
						padding: 0.4rem;
					}
				}
			}
		}

		.img {
			flex-grow: 0;
			max-width: 50%;
			flex-basis: 50%;

			@media only screen and (max-width:600px){
				display: none;
			}

			img {
				position: absolute ;
				top: 30px ;
				right: -15% ;
				width: 65% ;

				@media only screen and (max-width:800px){
					top: 10%;
				}
			}
		}

		.socials {
			margin: 1.5rem 0;

			a {
				text-decoration: none;
			}
		}
	}

	.line {
		width: 10%;
		margin: 0 auto;
		hr {
			border: 2px solid #f6d869;
			background-color: #f6d869;
			border-radius: 1rem;
		}
	}
}
