.section5_container {
  width: 100%;
  padding-bottom: 5rem;
  background-color: #10223c;
  position: relative;
  z-index: -2;
  overflow: hidden;

  @media only screen and (max-width: 1800px) {
    padding-bottom: 3rem;
  }
  @media only screen and (max-width: 1700px) {
    padding-bottom: 2rem;
  }
  @media only screen and (max-width: 1400px) {
    padding-bottom: 1rem;
  }

  .back {
    width: 100%;
    position: absolute;
    top: -8%;
    z-index: -1;

    @media only screen and (max-width: 1550px) {
      top: -6%;
    }
    @media only screen and (max-width: 1450px) {
      top: -2%;
    }
    @media only screen and (max-width: 1350px) {
      top: 5%;
    }
    @media only screen and (max-width: 800px) {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .heading {
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h1 {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 55px;
      text-align: center;
      color: #f6d869;
      padding-bottom: 25px;
      font-weight: 700;
    }
    hr {
      width: 100px;
      background-color: #f6d869;
      border: 1px solid #f6d869;
    }
  }

  .data {
    margin-top: 5rem;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    @media only screen and (max-width: 1650px) {
      margin-top: 3rem;
    }
    @media only screen and (max-width: 1400px) {
      margin-top: 2rem;
    }
    @media only screen and (max-width: 800px) {
      display: none;
    }

    .card {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 17%;

      @media only screen and (max-width: 800px) {
        width: 50%;
      }

      h1 {
        font-size: 20px;
        font-weight: 500;
        color: #fff;
      }
      .content {
        color: hsla(0, 0%, 89.8%, 0.6666666666666666);
        font-size: 12px;
        margin: 1rem 0;
        text-align: center;
        line-height: 1.3rem;

        @media only screen and (max-width: 1500px) {
          font-size: 11px;
        }
      }
      .year {
        border: 10px solid #f6d869;
        padding: 1.8rem 1.2rem;
        border-radius: 70%;
        font-size: 1.5rem;
        font-weight: bold;
        color: #fff;
      }
      p {
        margin: 1rem 0 2.5rem 0;
        color: #fff;
        text-align: center;
      }
      .line {
        position: relative;

        .border_down {
          position: absolute;
          top: 65%;
          right: 98%;
          border: 2px dashed #f6d869;
          width: 235%;
          transform: rotate(340deg);

          @media only screen and (max-width: 1850px) {
            top: 71%;
            right: 93%;
            transform: rotate(334deg);
          }
          @media only screen and (max-width: 1750px) {
            top: 71%;
            right: 93%;
            transform: rotate(334deg);
            width: 215%;
          }
          @media only screen and (max-width: 1650px) {
            top: 71%;
            right: 95%;
            transform: rotate(334deg);
            width: 195%;
          }
          @media only screen and (max-width: 1550px) {
            top: 70%;
            right: 92%;
            transform: rotate(332deg);
            width: 180%;
          }
          @media only screen and (max-width: 1450px) {
            top: 74%;
            right: 92%;
            transform: rotate(330deg);
            width: 160%;
          }
        }
        .border_up {
          position: absolute;
          top: -10%;
          right: 98%;
          border: 2px dashed #f6d869;
          width: 235%;
          transform: rotate(22deg);

          @media only screen and (max-width: 1850px) {
            top: -18%;
            right: 98%;
            width: 220%;
            transform: rotate(24deg);
          }
          @media only screen and (max-width: 1750px) {
            right: 92%;
            width: 215%;
            transform: rotate(28deg);
          }
          @media only screen and (max-width: 1650px) {
            width: 195%;
          }
          @media only screen and (max-width: 1550px) {
            width: 180%;
          }
          @media only screen and (max-width: 1450px) {
            width: 165%;
          }
        }
      }
    }
  }
}
